@font-face {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 100;
  src: local('Kanit Thin'), local('Kanit-Thin'), url(http://fonts.gstatic.com/s/kanit/v3/nKKX-Go6G5tXcr72KwKAdQ.ttf) format('truetype');
}
@font-face {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 400;
  src: local('Kanit Regular'), local('Kanit-Regular'), url(http://fonts.gstatic.com/s/kanit/v3/nKKZ-Go6G5tXcraVGwU.ttf) format('truetype');
}
@font-face {
  font-family: 'Kanit';
  font-style: normal;
  font-weight: 700;
  src: local('Kanit Bold'), local('Kanit-Bold'), url(http://fonts.gstatic.com/s/kanit/v3/nKKU-Go6G5tXcr4uPhWnVaQ.ttf) format('truetype');
}

@keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(1);
  }
  to {
    opacity: 1;
    transform: scale(1.1);
  }
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: 'Kanit', sans-serif;
  font-size: 1.6rem;
  color: #2d2d2d;
  background: #f4f4f4;
}
@media only screen and (max-width: 559px) {
  body {
    font-size: 1.4rem;
  }
}
h3 {
  font-size: 2.8rem;
}
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}
.bg-light {
  background: #f4f4f4;
}
.one-third {
  width: 33.3%;
}
.one-half {
  width: 50%;
}
@media only screen and (max-width: 767px) {
  .xs-full {
    justify-content: center;
    width: 100%;
  }
}
.flex {
  display: flex;
}
.column {
  flex-direction: column;
}
.row {
  flex-direction: row;
}
@media only screen and (max-width: 767px) {
  .xs-column {
    flex-direction: column;
  }
}
.grow {
  flex-grow: 1;
}
.center {
  justify-content: center;
  text-align: center;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
@media only screen and (max-width: 1280px) {
  .container {
    width: 100%;
    padding: 0 3rem;
    margin: 0;
  }
}
.logo {
  display: block;
  position: absolute;
  top: 2.1rem;
}
.logo svg {
  fill: #d80027;
}
.header {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  transition: 0.3s ease;
  overflow: hidden;
  width: 100%;
  height: 7.2rem;
  z-index: 100;
}
.header .container {
  position: relative;
}
.header.active,
.header.open {
  background: rgba(45, 45, 45, 0.9);
}
@media only screen and (max-width: 767px) {
  .header {
    display: block;
  }
  .header.open {
    height: 18.8rem;
  }
}
.header ul {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: flex-end;
  padding: 0;
}
@media only screen and (max-width: 767px) {
  .header ul {
    display: block;
    padding-top: 6rem;
  }
}
.header ul li {
  display: inline-block;
  list-style: none;
}
.header ul li a {
  color: #d80027;
  font-size: 1.6rem;
  font-weight: bold;
  text-decoration: none;
  padding-left: 6rem;
  letter-spacing: .05rem;
  transition: 0.15s ease;
  text-transform: uppercase;
}
@media only screen and (max-width: 767px) {
  .header ul li a {
    display: block;
    padding: 1.4rem 0;
  }
}
.header ul li a:hover {
  text-decoration: underline;
}
@media only screen and (max-width: 767px) {
  .header ul li {
    display: block;
  }
}
.header ul li.hamburger {
  border-radius: 50%;
  cursor: pointer;
  display: none;
  padding: 1.7rem 1.5rem;
  overflow: hidden;
  position: absolute;
  right: 1rem;
  top: 1rem;
  transition: 0.15s ease;
}
.header ul li.hamburger:hover {
  background: rgba(45, 45, 45, 0.2);
}
@media only screen and (max-width: 767px) {
  .header ul li.hamburger {
    display: block;
  }
}
.header ul li.hamburger span {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  width: 2rem;
  height: .3rem;
  background: #d80027;
  text-indent: -20rem;
}
.header ul li.hamburger span:before,
.header ul li.hamburger span:after {
  content: "";
  display: block;
  width: 100%;
  height: .3rem;
  background: #d80027;
  position: absolute;
  left: 0;
}
.header ul li.hamburger span:before {
  top: -0.7rem;
}
.header ul li.hamburger span:after {
  bottom: -0.7rem;
}
.banner {
  background: #383838;
  min-height: 400px;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
@media only screen and (max-width: 767px) {
  .banner {
    background: no-repeat center top url(../images/banner.jpg);
    background-size: auto 100%;
  }
}
.banner:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: no-repeat center top url(../images/banner.jpg);
  background-size: cover;
  background-attachment: fixed;
  animation: fadeIn 3.5s ease .5s 1 forwards;
  opacity: 0;
}
@media only screen and (max-width: 767px) {
  .banner:after {
    display: none;
  }
}
.banner h1 {
  color: white;
  font-size: 7rem;
  font-weight: normal;
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .04em;
  position: relative;
  z-index: 1;
}
@media only screen and (max-width: 767px) {
  .banner h1 {
    font-size: 5rem;
  }
}
.banner .wrap:after {
  content: "";
  display: inline-block;
  height: .9em;
  border-right: 4px solid #d80027;
  margin-bottom: -0.15em;
  animation: blink 500ms infinite;
}
.benefit {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 4rem 2rem 4rem 0;
}
@media only screen and (max-width: 767px) {
  .benefit {
    margin-right: 0;
  }
}
.benefit img {
  display: block;
  margin-right: 2rem;
}
.benefit h2 {
  font-size: 1.8rem;
  font-weight: normal;
}
.benefit h2 strong {
  font-size: 3.6rem;
  font-weight: bold;
  display: block;
}
.section-header {
  color: rgba(191, 191, 191, 0.5);
  font-size: 6rem;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 1rem 0 6rem;
  letter-spacing: .1rem;
}
.section-header:before {
  content: "...";
  display: block;
  color: #2d2d2d;
  margin-bottom: 8rem;
}
@media only screen and (max-width: 767px) {
  .section-header {
    font-size: 5rem;
  }
}
.skills {
  max-width: 90rem;
  margin: 0 auto;
}
.list {
  padding: 0 0 3rem 3rem;
}
.list li {
  list-style: none;
  margin: 0 0 1rem;
  position: relative;
}
.list li:before {
  content: "";
  display: block;
  position: absolute;
  left: -3rem;
  top: .2rem;
  background: url(../images/check.svg) no-repeat center center;
  width: 2.1rem;
  height: 1.6rem;
}
.logos {
  text-align: center;
  margin: 4rem 0;
}
.logos img {
  display: inline-block;
  margin: 0 2rem 2rem;
  opacity: .4;
  transition: 0.3s ease;
}
.logos img:hover {
  opacity: .8;
}
.map {
  background: #2d2d2d;
  min-height: 30rem;
}
.address-wrapper {
  width: 20rem;
  height: 0;
  position: absolute;
}
.address {
  background: white;
  padding: 1.5rem;
  display: flex;
  flex-direction: row;
  font-size: 1.6rem;
  font-family: 'Kanit', sans-serif;
  line-height: 1.1em;
  width: 20rem;
  position: absolute;
  transform: translate(30%, -100%);
}
.address a {
  display: block;
  margin-left: 2rem;
}
.address img {
  display: block;
}
.contacts .address {
  display: none;
}
.contacts {
  background: #2d2d2d;
  padding: 6rem 4rem 4rem;
}
.contact {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 0 4rem;
  position: relative;
}
.contact:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.5) url(../images/envelope.svg) no-repeat center center;
  border-radius: 50%;
  opacity: 0;
  transition: 0.15s ease;
}
.contact > span {
  transition: 0.15s ease;
}
.contact img {
  border-radius: 50%;
  display: block;
  margin-right: 2rem;
  transition: 0.15s ease;
}
.contact h4 {
  font-size: 2rem;
  margin: 0;
  transition: 0.15s ease;
}
.contact strong {
  font-weight: normal;
  display: block;
  opacity: .2;
  transition: 0.15s ease;
}
.contact:hover img {
  transform: scale(1.1);
}
.contact:hover > span {
  transform: translatex(1rem);
}
.contact:hover strong {
  color: #d80027;
  opacity: 1;
}
.contact:hover:after {
  opacity: 1;
}
.copyright {
  color: white;
  opacity: .2;
  margin-top: 8rem;
}
