// Normalize Styles
@import (inline) 'node_modules/normalize.css/normalize.css';
@import (inline) url('//fonts.googleapis.com/css?family=Kanit:100,400,700');

// Colors
@colorPrimary: #d80027;
@colorSecondary: #2d2d2d;
@colorGray: #bfbfbf;
@colorGrayLight: #d5d5d5;
@colorBg: #f4f4f4;

@fontMain: ~"'Kanit', sans-serif";

// Media queries
@xxs:       ~"only screen and (max-width: 559px)";
@xs:        ~"only screen and (max-width: 767px)";
@sm:        ~"only screen and (max-width: 999px)";
@sm-only:   ~"only screen and (min-width: 768px) and (max-width: 999px)";
@md:        ~"only screen and (max-width: 1280px)";
@md-only:   ~"only screen and (min-width: 1000px) and (max-width: 1280px)";
@lg:        ~"only screen and (min-width: 1280px)";
@print:     ~"only print";

// Animations
@transition: .3s ease;
@transitionShort: .15s ease;

@keyframes blink {
  from{ opacity: 1; }
  to{ opacity: 0; }
}

@keyframes fadeIn {
  from{ opacity: 0; transform: scale(1); }
  to{ opacity: 1; transform: scale(1.1); }
}

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: @fontMain;
  font-size: 1.6rem;
  color: @colorSecondary;
  background: @colorBg;

  @media @xxs {
    font-size: 1.4rem;
  }
}

h3 {
  font-size: 2.8rem;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

.bg-light {
  background: @colorBg;
}

.one-third {
  width: 33.3%;
}

.one-half {
  width: 50%;
}

.xs-full {
  @media @xs {
    justify-content: center;
    width: 100%;
  }
}

.flex {
  display: flex;
}

.column {
  flex-direction: column;
}

.row {
  flex-direction: row;
}

.xs-column {
  @media @xs {
    flex-direction: column;
  }
}

.grow {
  flex-grow: 1;
}

.center {
  justify-content: center;
  text-align: center;
}

.container {
  width: 1200px;
  margin: 0 auto;

  @media @md {
    width: 100%;
    padding: 0 3rem;
    margin: 0;
  }
}

.logo {
  display: block;
  position: absolute;
  top: 2.1rem;

  svg {
    fill: @colorPrimary;
  }
}

.header {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  transition: @transition;
  overflow: hidden;
  width: 100%;
  height: 7.2rem;
  z-index: 100;

  .container {
    position: relative;
  }

  &.active,
  &.open {
    background: fade(@colorSecondary, 90%);
  }

  @media @xs {
    display: block;

    &.open {
      height: 18.8rem;
    }
  }

  ul {
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: flex-end;
    padding: 0;

    @media @xs {
      display: block;
      padding-top: 6rem;
    }
    
    li {
      display: inline-block;
      list-style: none;

      a {
        color: @colorPrimary;
        font-size: 1.6rem;
        font-weight: bold;
        text-decoration: none;
        padding-left: 6rem;
        letter-spacing: .05rem;
        transition: @transitionShort;
        text-transform: uppercase;

        @media @xs {
          display: block;
          padding: 1.4rem 0;
        }

        &:hover {
          text-decoration: underline;
        }
      }

      @media @xs {
        display: block;
      }

      &.hamburger {
        border-radius: 50%;
        cursor: pointer;
        display: none;
        padding: 1.7rem 1.5rem;
        overflow: hidden;
        position: absolute;
        right: 1rem;
        top: 1rem;
        transition: @transitionShort;

        &:hover {
          background: fade(@colorSecondary, 20%);
        }
      
        @media @xs {
          display: block;
        }
      
        span {
          display: inline-block;
          vertical-align: middle;
          position: relative;
          width: 2rem;
          height: .3rem;
          background: @colorPrimary;
          text-indent: -20rem;
      
          &:before,
          &:after {
            content: "";
            display: block;
            width: 100%;
            height: .3rem;
            background: @colorPrimary;
            position: absolute;
            left: 0;
          }
      
          &:before {
            top: -.7rem;
          }
      
          &:after {
            bottom: -.7rem;
          }
      
        }
      }
    }
  }
}

.banner {
  background: #383838;
  min-height: 400px;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;

  @media @xs {
    background: no-repeat center top url(../images/banner.jpg);
    background-size: auto 100%;
  }

  &:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: no-repeat center top url(../images/banner.jpg);
    background-size: cover;
    background-attachment: fixed;
    animation: fadeIn 3.5s ease .5s 1 forwards;
    opacity: 0;

    @media @xs {
      display: none;
    }
  }

  h1 { 
    color: white;
    font-size: 7rem;
    font-weight: normal;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: .04em;
    position: relative;
    z-index: 1;

    @media @xs {
      font-size: 5rem;
    }
  }

  .wrap {
    &:after {
      content: "";
      display: inline-block;
      height: .9em;
      border-right: 4px solid @colorPrimary;
      margin-bottom: -.15em;
      animation: blink 500ms infinite;
    }
  }
}

.benefit {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 4rem 2rem 4rem 0;

  @media @xs {
    margin-right: 0;
  }

  img {
    display: block;
    margin-right: 2rem;
  }

  h2 {
    font-size: 1.8rem;
    font-weight: normal;

    strong {
      font-size: 3.6rem;
      font-weight: bold;
      display: block;
    }
  }
}

.section-header {
  color: fade(@colorGray, 50%);
  font-size: 6rem;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 1rem 0 6rem;
  letter-spacing: .1rem;

  &:before {
    content: "...";
    display: block;
    color: @colorSecondary;
    margin-bottom: 8rem;
  }

  @media @xs {
    font-size: 5rem;
  }
}

.skills {
  max-width: 90rem;
  margin: 0 auto;
}

.list {
  padding: 0 0 3rem 3rem;
  li {
    list-style: none;
    margin: 0 0 1rem;
    position: relative;

    &:before {
      content: "";
      display: block;
      position: absolute;
      left: -3rem;
      top: .2rem;
      background: url(../images/check.svg) no-repeat center center;
      width: 2.1rem;
      height: 1.6rem;
      
    }
  }
}

.logos {
  text-align: center;
  margin: 4rem 0;

  img {
    display: inline-block;
    margin: 0 2rem 2rem;
    opacity: .4;
    transition: @transition;

    &:hover {
      opacity: .8;
    }
  }
}

.map {
  background: @colorSecondary;
  min-height: 30rem;
}

.address-wrapper {
  width: 20rem;
  height: 0;
  position: absolute;
}

.address {
  background: white;
  padding: 1.5rem;
  display: flex;
  flex-direction: row;
  font-size: 1.6rem;
  font-family: @fontMain;
  line-height: 1.1em;
  width: 20rem;
  position: absolute;
  transform: translate(30%, -100%);

  a {
    display: block;
    margin-left: 2rem;
  }

  img {
    display: block;
  }

  .contacts & {
    display: none;
  }
}

.contacts {
  background: @colorSecondary;
  padding: 6rem 4rem 4rem;
}

.contact {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 0 4rem;
  position: relative;

  &:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 80px;
    height: 80px;
    background: rgba(255,255,255,.5) url(../images/envelope.svg) no-repeat center center;
    border-radius: 50%;
    opacity: 0;
    transition: @transitionShort;
  }

  & > span {
    transition: @transitionShort;    
  }

  img {
    border-radius: 50%;
    display: block;
    margin-right: 2rem;
    transition: @transitionShort;
  }

  h4 {
    font-size: 2rem;
    margin: 0;
    transition: @transitionShort;
  }

  strong {
    font-weight: normal;
    display: block;
    opacity: .2;
    transition: @transitionShort;    
  }

  &:hover {
    img {
      transform: scale(1.1);
    }

    & > span {
      transform: translatex(1rem);
    }

    strong {
      color: @colorPrimary;
      opacity: 1;
    }

    &:after {
      opacity: 1;
    }
  }
}

.copyright {
  color: white;
  opacity: .2;
  margin-top: 8rem;
}